
import { Check, Star, Zap, Crown } from "lucide-react"
import styles from "./plans.module.css"
import { useNavigate } from "react-router-dom"

// Custom components
const Card = ({ children, className, style }) => (
  <div className={`${styles.card} ${className || ""}`} style={style}>
    {children}
  </div>
)

const Button = ({ children, className, onClick, variant = "default", ...props }) => (
  <button
    onClick={onClick}
    className={`${styles.button} ${styles[`button${variant.charAt(0).toUpperCase() + variant.slice(1)}`]} ${className || ""}`}
    {...props}
  >
    {children}
  </button>
)

const Badge = ({ children, className }) => <span className={`${styles.badge} ${className || ""}`}>{children}</span>

export function Plans() {
  const navigate = useNavigate();

  const websitePlans = [
    {
      name: "Bsico",
      price: "R$500",
      period: "em media",
      description: "LandingPage: Ideal para pequenos negcios e profissionais liberais",
      features: [
        "Pagina responsivo",
        "Design personalizado",
        "Formulrio de contato",
        "SEO bsico",
        "SSL gratuito",
        "Formulario de Contato",
      ],
      icon: Star,
      popular: false,
    },
    {
      name: "Profissional",
      price: "R$1.500",
      period: "em media",
      description: "Site institucional: Para empresas que precisam de mais funcionalidades",
      features: [
        "Todas funes do plano basic",
        "Site responsivo at 5 pginas",
        "Design premium personalizado",
        "Sistema de blog/notcias",
        "Integrao com redes sociais",
        "SEO avanado",
        "5 revises includas",
      ],
      icon: Zap,
      popular: true,
    },
    {
      name: "Enterprise",
      price: "R$4.000",
      period: "em media",
      description: "Soluo completa para grandes empresas",
      features: [
        "Todas funes do plano Profissional",
        "Site responsivo ilimitado",
        "Design exclusivo e identidade visual",
        "Painel administrativo",
        "Integrao com sistemas",
        "SEO premium",
        "Relatrios avanados",
        "Suporte prioritrio",
        "Sites Multilinguagens"
      ],
      icon: Crown,
      popular: false,
    },
  ]

  const maintenancePlans = [
    {
      name: "Essencial",
      price: "R$150",
      period: "/ms",
      description: "Manuteno bsica para manter seu site funcionando",
      features: [
        "Hospedagem premium",
        "SSL renovado automaticamente",
        "Backup semanal",
        "Monitoramento 24/7",
        "Suporte por email e Whatsapp",
        "Alteraes a parte",
      ],
      icon: Star,
      popular: false,
    },
    {
      name: "Avanado",
      price: "R$200",
      period: "/ms",
      description: "Para sites que precisam de ateno constante",
      features: [
        "Tudo do plano Essencial",
        "Backup dirio",
        "Atualizaes de segurana",
        "Relatrios mensais",
        "Suporte prioritrio",
        "5 horas de alteraes/ms",
        "Otimizao de performance",
      ],
      icon: Zap,
      popular: true,
    },
    {
      name: "Premium",
      price: "R$450",
      period: "/ms",
      description: "Suporte completo e dedicado para seu negcio",
      features: [
        "Tudo do plano Avanado",
        "Suporte 24/7 por telefone",
        "Alteraes ilimitadas",
        "Consultoria estratgica",
        "Anlise de concorrncia",
        "Campanhas de marketing",
        "Segurana avanada",
      ],
      icon: Crown,
      popular: false,
    },
  ]

  const handleChoseService = (plan, manuten) => {
    if(manuten) return
    localStorage.setItem("plan", plan)
    navigate("/new")
  }

  const PlanCard = ({ plan, index, manuten }) => (
    <Card
      className={`${styles.planCard} ${plan.popular ? styles.planCardPopular : ""}`}
      style={{ animationDelay: `${index * 0.1}s` }}
    >
      {plan.popular && <Badge className={styles.popularBadge}>Mais Popular</Badge>}

      <div id="plans" className={styles.planHeader}>
        <div className={styles.planIcon}>
          <plan.icon className={styles.icon} />
        </div>
        <h3 className={styles.planName}>{plan.name}</h3>
        <p className={styles.planDescription}>{plan.description}</p>
        <div className={styles.planPricing}>
          <span className={styles.planPrice}>{plan.price}</span>
          <span className={styles.planPeriod}>{plan.period}</span>
        </div>
      </div>

      <ul className={styles.featuresList}>
        {plan.features.map((feature, featureIndex) => (
          <li key={featureIndex} className={styles.featureItem}>
            <Check className={styles.checkIcon} />
            <span>{feature}</span>
          </li>
        ))}
      </ul>

      <Button
        className={`${styles.planButton} ${plan.popular ? styles.planButtonPopular : ""}`}
        variant={plan.popular ? "default" : "outline"}
        onClick={() => {
          handleChoseService(plan.name, manuten)
        }}
      >
        {plan.popular ? "Escolher Plano" : "Saiba Mais"}
      </Button>
    </Card>
  )

  return (
    <section id="planos" className={styles.plans}>
      <div className={styles.container}>
        {/* Website Plans */}
        <div className={styles.planSection}>
          <div className={styles.header}>
            <h2 className={styles.title}>
              Planos de <span className={styles.titleHighlight}>Desenvolvimento</span>
            </h2>
            <p className={styles.subtitle}>
              Escolha o plano ideal para criar seu site profissional com tecnologia de ponta.
            </p>
          </div>

          <div className={styles.plansGrid}>
            {websitePlans.map((plan, index) => (
              <PlanCard key={index} plan={plan} index={index} />
            ))}
          </div>
        </div>

        {/* Maintenance Plans */}
        <div className={styles.planSection}>
          <div className={styles.header}>
            <h2 className={styles.title}>
              Planos de <span className={styles.titleHighlight}>Manuteno</span>
            </h2>
            <p className={styles.subtitle}>Mantenha seu site sempre atualizado, seguro e funcionando perfeitamente.</p>
          </div>

          <div className={styles.plansGrid}>
            {maintenancePlans.map((plan, index) => (
              <PlanCard manuten={true} key={index} plan={plan} index={index} />
            ))}
          </div>
        </div>
      </div>
    </section>
  )
}
